@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;700;800;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
:root {
  --primary-color: #0a3971;
  --secondary-color: #f4f4f4;
  --tertiary-color: #feb800;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
  font-size: 16px;
}

body {
  background-color: var(--primary-color);
}

html {
  scroll-behavior: smooth;
}

.landing-header {
  height: 80px;

  display: flex;
  align-items: center;
  justify-content: space-between;
}

.landing-header > nav {
  width: 500px;
  padding: 20px;
}
.log {
  background-color: transparent;
}
.landing-login {
  border: 2.5px solid var(--tertiary-color);
  padding: 5px 40px;
  border-radius: 10px;
  color: var(--secondary-color);
  font-size: 16px;
  background-color: transparent;
}

.landing-signup {
  border: 1px solid var(--tertiary-color);
  padding: 5px 40px;
  border-radius: 10px;
  color: var(--secondary-color);
  font-size: 16px;
  background-color: var(--tertiary-color);
}
.kazi-header {
  font-size: 25px;
  padding: 10px;
}

.kazi-header > span {
  font-size: 25px;
  color: var(--tertiary-color);
}

#image-container > img {
  max-width: 100%;
  height: 600px;
  display: block;
  margin: 0 auto;
}

#image-container {
  background-image: url(/static/media/blob.0e7144e9.svg);
  background-repeat: no-repeat;
  background-position: 100%;
  background-size: cover;
}

.top-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 82vh;
  padding: 20px 30px;
}

.top-container > div {
  flex: 50% 1;
}

#start-section {
  padding: 20px 20px;
  text-align: center;
  border: 1px solid var(--secondary-color);
  border-radius: 15px;
}
h2 > #kazi {
  color: var(--tertiary-color);
}

#start-section > h2,
#kazi {
  word-spacing: 30px;
  font-size: 40px;
  color: var(--secondary-color);
}

#get-started {
  padding: 10px 30px;
  border-radius: 15px;
  border: none;
  font-weight: 700;
  font-size: 18px;
  margin-top: 20px;
  background-color: var(--tertiary-color);
  cursor: pointer;
  transition: all 0.5s ease;
}

#get-started:hover {
  border: 2px solid var(--tertiary-color);
  background-color: transparent;
  color: var(--secondary-color);
}

.why-us {
  display: flex;

  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  margin-top: 50px;
  background-color: var(--secondary-color);
  padding: 10px 30px;
  height: 300px;
}

.numbers {
  padding: 40px;
  background-color: var(--secondary-color);
  border-radius: 10px;
  color: var(--primary-color);
  font-size: 18px;
  box-shadow: 2.8px 2.8px 2.2px rgba(0, 0, 0, 0.02),
    6.7px 6.7px 5.3px rgba(0, 0, 0, 0.028),
    12.5px 12.5px 10px rgba(0, 0, 0, 0.035),
    22.3px 22.3px 17.9px rgba(0, 0, 0, 0.042),
    41.8px 41.8px 33.4px rgba(0, 0, 0, 0.05),
    100px 100px 80px rgba(0, 0, 0, 0.07);
  border: 1px solid var(--tertiary-color);
}

.plus {
  width: 18px;
  height: 18px;
}
.about-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  margin-top: 50px;
}

.landing-img > img {
  width: 100%;
  border-radius: 10px;
}

.landing-2 {
  display: block;
  text-align: left;
  padding: 20px;
}

.landing-2 > span {
  display: block;
  color: var(--tertiary-color);
  border-left: 4px solid var(--tertiary-color);
  padding: 10px;
}

.landing-2 > h2 {
  text-align: left;
  font-size: 20px;
  word-spacing: 20px;
}
.landing-1 {
  padding: 20px;
}

.landing-3 {
  color: var(--secondary-color);

  padding: 15px;
}

.landing-3 > p {
  font-size: 15px;
}

.circle {
  padding: 10px;
  font-weight: 400;
}

.circle::before {
  content: "• ";
  color: var(--tertiary-color);
  font-size: 20px;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}
.jobs-descriptions {
  display: flex;
  align-items: center;
  width: 95%;
  margin: 0 auto;
  margin-top: 50px;
}



.jobs-1 {
  padding: 15px;
  color: var(--secondary-color);
}

.jobs-1 > p {
  font-size: 14px;
  border-bottom: 2px solid var(--tertiary-color);
}

.jobs-1 > h3 {
  color: var(--tertiary-color);
  letter-spacing: 2px;
}

.job-icons {
  color: var(--tertiary-color);
  width: 30px;
  height: 30px;
}
.customer-testimonals {
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  border: 1px solid rgb(221, 217, 217);
  width: 95%;
  margin: 0 auto;
  margin-top: 50px;
  border-radius: 10px;
  margin-bottom: 30px;
  background-color: var(--secondary-color);
}

.part-1 {
  flex: 0.5 1;
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100%;
}
.part-2 {
  flex: 2 1;
  color: var(--secondary-color);
  text-align: center;
}

.quotes {
  width: 50px;
  height: 50px;
  color: var(--tertiary-color);
  margin-top: 40px;
}

.part-2 {
  background-color: #154d91;
  padding: 20px 0px;
}

.part-2 > h3 {
  font-size: 40px;
}

.part-2 > h3::after {
  content: "";
  border-bottom: 4px solid var(--tertiary-color);
  top: 1970px;
  position: absolute;
  width: 8%;
  left: 46%;
}

.part-2 > p {
  font-size: 15px;
  font-weight: 400;
}

.part-1 > img {
  display: block;
  border-radius: 50%;
  width: 100px;
  height: 100px;
}

.expense-form-container {
  /* background-image: url("/public/images/img1.png"); */
  background-repeat: no-repeat;
  background-size: 100% 100%;
  padding: 10px;
  overflow: hidden;
}

#expense-signup {
  background-color: var(--primary-color);
  width: 80%;
  border-radius: 10px;
  margin: 0 auto;
  padding: 20px;
}

.row-1,
.row-2,
.row-3,
.row-4,
.row-5 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
}

#textarea {
  padding: 10px;
  outline: none;
  border-radius: 12px;
}

.col-1 > input {
  border: none;
  font-size: 15px;
  outline: none;
  color: var(--secondary-color);
  padding: 10px 15px;
  background-color: var(--primary-color);
  border-bottom: 2px solid var(--tertiary-color);
}

.col-1 > label {
  font-weight: 600;
  color: var(--secondary-color);
}

.expense-submit-btn {
  display: flex;
  justify-content: center;
}

.expense-submit-btn > button {
  padding: 10px 45px;
  border-radius: 10px;
  border: none;
  background-color: var(--tertiary-color);
  color: var(--primary-color);
  font-weight: 600;
  cursor: pointer;
}

#expense-signup h2 {
  color: var(--secondary-color);
  text-align: center;
  margin-top: 10px;
}

.signup-error {
  color: var(--tertiary-color);
  font-weight: 400;
  font-size: 13px;
}

.button-span {
  background-color: #feb800;
  border: none;
  border-radius: 10px;
  padding: 5px 30px;
  font-weight: 600;
  color: black;
  cursor: pointer;
}
.image > img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  object-fit: cover;
  border: 2px solid var(--tertiary-color);
}
.image {
  padding: 5px 10px;
}
.name {
  color: #feb800;
  font-family: Poppins;
  font-size: 15px;
  text-align: center;
}
.occupation {
  font-size: 21px;
  font-family: Poppins;
  color: #f4f4f4;
  text-align: center;
  font-size: 12px;
}
.expensecard {
  display: flex;
  align-items: center;
  width: 100%;
  border-radius: 10px;
}
.info-bottom {
  padding-top: 20px;
}
.rating,
.button-span {
  padding-left: 30px;
}
.rating,
.location {
  font-weight: 600;
  font-size: 15px;
  color: var(--secondary-color);
}
.info {
  padding-top: 2%;
  padding-bottom: 5%;
  padding-left: 8%;
  flex: 1 1;
}
.info-top {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.container {
  width: 100%;
  background-color: #0a3971;
  border-radius: 20px;
  margin-bottom: 70px;
}

.main-container {
  padding: 10px;
}

.description-heading {
  font-size: 18px;
  font-weight: 800;
  color: var(--tertiary-color);
  text-align: center;
  line-height: 1.5;
}
.paragraph {
  padding-top: 3%;
  color: var(--secondary-color);
  font-size: 15px;
  text-align: justify;
  padding: 0px 10px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-right: 20px;
  word-spacing: 8px;
}

.handy-page {
  background-color: #f4f4f4;
  height: 100%;
  padding-left: 10%;
  padding-top: 5%;
  padding-bottom: 10%;
}
.top-image {
  height: 200px;
  width: 200px;
  border-radius: 15px;
  object-fit: cover;
}
.page-top {
  display: flex;
  grid-gap: 100px;
  gap: 100px;
  margin-bottom: 100px;
}
.top-div {
  align-items: center;
}
.header1 {
  font-size: 30px;
  padding-left: 30%;
}
.top-header {
  font-weight: 200;
  font-size: 18px;
  color: var(--primary-color);
}

.expense-search {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 80%;
  margin-left: 16%;
  margin-bottom: 50px;
}
.search-icon {
  height: 40px;
  width: 40px;
}
.sidebar-card {
  display: flex;
  grid-gap: 10%;
  gap: 10%;
}
.filter {
  height: 40px;
  width: 300px;
}
.option-div {
  padding-bottom: 20%;
}
.option-div > h3 {
  font-size: 20px;
}

.handy-page-image {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.img-1 {
  padding: 10px;
  margin: 0 auto;
  flex: 1 1;
}

.mans {
  border-radius: 50%;
  width: 400px;
  height: 400px;
  border: 4px solid var(--secondary-color);
  margin: 0 auto;
  display: block;
}

.tools1 {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  margin: 10px;
  top: 10%;
  left: 60%;
  border: 2px solid var(--secondary-color);
  z-index: 1;
}
.tools2 {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  margin: 10px;
  top: 35%;
  left: 55%;
  border: 2px solid var(--secondary-color);
}

.tools3 {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  position: absolute;
  display: block;
  margin: 10px;
  top: 60%;
  left: 62%;
  border: 2px solid var(--secondary-color);
}

.handy-page-info > h2 {
  text-align: left;
  font-size: 30px;
  word-spacing: 20px;
  color: var(--tertiary-color);
}

.handy-page-info {
  padding: 15px;

  flex: 1 1;
  height: 300px;
}

.handy-page-info > p {
  font-size: 13px;
  margin-top: 20px;
  color: var(--secondary-color);
}

.handy-page-info > span {
  background-color: var(--tertiary-color);
  padding: 5px 40px;
  margin-top: 40px;
  display: block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-radius: 5px;
}

.login-form-container {
  /* background-image: url("/public/images/img1.png"); */
  background-repeat: no-repeat;
  background-size: 100% 100%;
  overflow: hidden;
  height: 83vh;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 24px 12px;
}

#login-form {
  background-color: var(--primary-color);
  width: 70%;
  border-radius: 0.5rem;
  margin: 0 auto;
  padding: 20px;
  display: grid;
  grid-row-gap: 6px;
  width: 70%;
}

.row {
  justify-content: center;
  font-weight: 600;
  color: var(--secondary-color);
}
.rows > input {
  border: none;
  outline: none;
  color: var(--secondary-color);
  padding: 10px 15px;
  background-color: var(--primary-color);
  border-bottom: 2px solid var(--tertiary-color);
  margin-bottom: 0rem;
}

.login-btn {
  display: flex;
  justify-content: center;
}

.login-btn > button {
  padding: 10px 45px;
  border-radius: 10px;
  border: none;
  background-color: var(--tertiary-color);
  color: var(--primary-color);
  font-weight: 600;
  cursor: pointer;
  margin-top: 2rem;
}
h2 {
  color: var(--secondary-color);
  text-align: center;
  margin-top: 10px;
}
.login-errors {
  font-size: 12px;
  font-weight: 400;
  color: var(--tertiary-color);
}

.extra {
  overflow: hidden;
  display: flex;
  align-items: center;
  text-align: center;
  justify-content: center;
}
h1 .extra {
  text-align: center;
  padding-left: 30px;
}
#login-form h1 {
  align-items: center;
  text-align: center;
  color: #fff;
  font-weight: bold;
  font-size: 40px;
}
@media only screen and (max-width: 900px) {
  .login-form-container {
    justify-content: center;
  }
}

* {
  list-style: none;
}
.header {
  display: flex;
  grid-gap: 20px;
  gap: 20px;
  color: var(--secondary-color);
  padding: 20px;
  height: 80px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 1;
  width: 100%;
  background-color: var(--primary-color);
}

#kazi-header {
  color: var(--tertiary-color);
}

#profile {
  background-color: var(--tertiary-color);
  padding: 10px;
  border-radius: 50%;
}

.header > nav {
  flex: 2 1;
  width: 80%;
}

nav > ul > li {
  font-size: 15px;
  font-weight: 700;
}

ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

#active-btn {
  color: var(--secondary-color);
  border: 1px solid var(--tertiary-color);
  background-color: var(--tertiary-color);
}

.row-update {
  width: 80%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 10px;
}
.row-update > img {
  display: block;
  border-radius: 50%;
  width: 200px;
  height: 200px;
  border: 4px solid var(--tertiary-color);
}

.image-profile {
  position: relative;
  left: 13%;
  bottom: 40px;
  cursor: pointer;
}

.row-update > h2,
.row-update > h3,
.row-update > h4,
.row-update > h5 {
  width: 50%;
  display: flex;
  justify-content: space-between;
  color: var(--secondary-color);
  align-items: center;
}

.row-update > h2 {
  color: var(--tertiary-color);
}

.row-update > h2,
.row-update > h3,
.row-update > h4 {
  border: 1px solid var(--secondary-color);
  border-radius: 10px;
  padding: 10px;
}

.row-update > p {
  width: 80%;
  color: var(--secondary-color);
  font-size: 15px;
  border: 1px solid var(--secondary-color);
  border-radius: 10px;
  padding: 10px;
}
.user-update {
  color: var(--primary-color);
  background-color: var(--secondary-color);
  border-radius: 50%;
  padding: 8px;
}

.pen {
  display: inline;
}

.updateForm > form > input {
  border: none;
  border-radius: 5px 0px 0px 5px;
  padding: 5px 30px;
  width: 40%;
  outline: none;
}

.updateForm > form > button {
  padding: 6px 30px;
  border-radius: 0px 5px 5px 0px;
  border: none;
  background-color: var(--tertiary-color);
  font-size: 15px;
  font-weight: bold;
  color: var(--primary-color);
}

.updateForm > form {
  width: 100%;
}
.updateForm {
  width: 100%;
  display: block;
  margin-top: 20px;
}

.text-update {
  display: block;
  border-radius: 10px;
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.alert {
  border: 1px solid red;
  width: 500px;
  height: 200px;
  padding: 10px 30px;
  border-radius: 15px;
  background-color: var(--secondary-color);
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.alert > p,
.alert > button {
  color: var(--primary-color);
}
.alert > p {
  font-size: 20px;
  font-weight: bold;
}

.alert > button {
  border: none;
  background-color: var(--primary-color);
  color: var(--tertiary-color);
  font-size: 15px;
  font-weight: 600;
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
}

.footer-style {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 30px;
  margin: 0 auto;
  border-top: 1px solid var(--secondary-color);
}

#kazi-footer {
  color: var(--tertiary-color);
  font-size: 25px;
}

.footer-style > h2 {
  font-size: 25px;
}

#copy-right {
  display: block;
  color: var(--secondary-color);
  font-size: 25px;
  font-weight: 700;
}

.social-icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 20%;
}
.social-icon {
  display: block;
  color: var(--tertiary-color);
  font-size: 30px;
}

/* Main CSS Here */

* {
margin: 0;
padding: 0;
box-sizing: border-box;
font-family: "Poppins", sans-serif;
}
:root {
--background-color1: #fafaff;
--background-color2: #ffffff;
--background-color3: #ededed;
--background-color4: #cad7fda4;
--primary-color: #4b49ac;
--secondary-color: #0c007d;
--Border-color: #3f0097;
--one-use-color: #3f0097;
--two-use-color: #5500cb;
}
body {
background-color: var(--background-color4);
max-width: 100%;
overflow-x: hidden;
}

header {
height: 70px;
width: 100vw;
padding: 0 30px;
background-color: var(--background-color1);
position: fixed;
z-index: 100;
box-shadow: 1px 1px 15px rgba(161, 182, 253, 0.825);
display: flex;
justify-content: space-between;
align-items: center;
}

.logo {
font-size: 27px;
font-weight: 600;
color: rgb(47, 141, 70);
}

.icn {
height: 30px;
}
.menuicn {
cursor: pointer;
}

.searchbar,
.message,
.logosec {
display: flex;
align-items: center;
justify-content: center;
}

.searchbar2 {
display: none;
}

.logosec {
grid-gap: 60px;
gap: 60px;
}

.searchbar input {
width: 250px;
height: 42px;
border-radius: 50px 0 0 50px;
background-color: var(--background-color3);
padding: 0 20px;
font-size: 15px;
outline: none;
border: none;
}
.searchbtn {
width: 50px;
height: 42px;
display: flex;
align-items: center;
justify-content: center;
border-radius: 0px 50px 50px 0px;
background-color: var(--secondary-color);
cursor: pointer;
}

.message {
grid-gap: 40px;
gap: 40px;
position: relative;
cursor: pointer;
}
.circle {
height: 7px;
width: 7px;
position: absolute;
background-color: #fa7bb4;
border-radius: 50%;
left: 19px;
top: 8px;
}
.dp {
height: 40px;
width: 40px;
background-color: #626262;
border-radius: 50%;
display: flex;
align-items: center;
justify-content: center;
overflow: hidden;
}
.main-container {
display: flex;
width: 20vw;
position: relative;
top: 70px;
z-index: 1;
}
.dpicn {
height: 42px;
}

.main {
height: calc(100vh - 70px);
width: 100%;
overflow-y: scroll;
overflow-x: hidden;
padding: 140px 70px 30px 0px;
}

.main::-webkit-scrollbar-thumb {
background-image:
		linear-gradient(to bottom, rgb(0, 0, 85), rgb(0, 0, 50));
}
.main::-webkit-scrollbar {
width: 5px;
}
.main::-webkit-scrollbar-track {
background-color: #9e9e9eb2;
}

.box-container {
display: flex;
justify-content: space-evenly;
align-items: center;
flex-wrap: wrap;
grid-gap: 50px;
gap: 50px;
}
.nav {
min-height: 89vh;
width: 250px;
background-color: var(--background-color2);
position: absolute;
top: 0px;
left: 00;
box-shadow: 1px 1px 10px rgba(198, 189, 248, 0.825);
display: flex;
flex-direction: column;
justify-content: space-between;
overflow: hidden;
padding: 30px 0 20px 10px;
}
.navcontainer {
height: calc(100vh - 70px);
width: 250px;
position: relative;
overflow-y: scroll;
overflow-x: hidden;
transition: all 0.5s ease-in-out;
}
.navcontainer::-webkit-scrollbar {
display: none;
}
.navclose {
width: 80px;
}
.nav-option {
width: 250px;
height: 60px;
display: flex;
align-items: center;
padding: 0 30px 0 20px;
grid-gap: 20px;
gap: 20px;
transition: all 0.1s ease-in-out;
}
.nav-option:hover {
border-left: 5px solid #a2a2a2;
background-color: #dadada;
cursor: pointer;
}
.nav-img {
height: 30px;
}

.nav-upper-options {
display: flex;
flex-direction: column;
align-items: center;
grid-gap: 30px;
gap: 30px;
}

.option1 {
border-left: 5px solid #010058af;
background-color: var(--Border-color);
color: white;
cursor: pointer;
}
.option1:hover {
border-left: 5px solid #010058af;
background-color: var(--Border-color);
}
.box {
height: 130px;
width: 230px;
border-radius: 20px;
box-shadow: 3px 3px 10px rgba(0, 30, 87, 0.751);
padding: 20px;
display: flex;
align-items: center;
justify-content: space-around;
cursor: pointer;
transition: transform 0.3s ease-in-out;
}
.box:hover {
transform: scale(1.08);
}

.box:nth-child(1) {
background-color: var(--one-use-color);
}
.box:nth-child(2) {
background-color: var(--two-use-color);
}
.box:nth-child(3) {
background-color: var(--one-use-color);
}
.box:nth-child(4) {
background-color: var(--two-use-color);
}

.box img {
height: 50px;
}
.box .text {
color: white;
}
.topic {
font-size: 13px;
font-weight: 400;
letter-spacing: 1px;
}

.topic-heading {
font-size: 30px;
letter-spacing: 3px;
}

.report-container {
min-height: 300px;
max-width: 1200px;
margin: 70px auto 0px auto;
background-color: #ffffff;
border-radius: 30px;
box-shadow: 3px 3px 10px rgb(188, 188, 188);
padding: 0px 20px 20px 20px;
}
.report-header {
height: 80px;
width: 100%;
display: flex;
align-items: center;
justify-content: space-between;
padding: 20px 20px 10px 20px;
border-bottom: 2px solid rgba(0, 20, 151, 0.59);
}

.recent-Articles {
font-size: 30px;
font-weight: 600;
color: #5500cb;
}

.view {
height: 35px;
width: 90px;
border-radius: 8px;
background-color: #5500cb;
color: white;
font-size: 15px;
border: none;
cursor: pointer;
}

.report-body {
max-width: 1160px;
overflow-x: auto;
padding: 20px;
}
.report-topic-heading,
.item1 {
width: 1120px;
display: flex;
justify-content: space-between;
align-items: center;
}
.t-op {
font-size: 18px;
letter-spacing: 0px;
}

.items {
width: 1120px;
margin-top: 15px;
}

.item1 {
margin-top: 20px;
}
.t-op-nextlvl {
font-size: 14px;
letter-spacing: 0px;
font-weight: 600;
}

.label-tag {
width: 100px;
text-align: center;
background-color: rgb(0, 177, 0);
color: white;
border-radius: 4px;
}


/* Responsive CSS Here */
@media screen and (max-width: 950px) {
    .nav-img {
        height: 25px;
    }
    .nav-option {
        grid-gap: 30px;
        gap: 30px;
    }
    .nav-option h3 {
        font-size: 15px;
    }
    .report-topic-heading,
    .item1,
    .items {
        width: 800px;
    }
    }

    @media screen and (max-width: 850px) {
    .nav-img {
        height: 30px;
    }
    .nav-option {
        grid-gap: 30px;
        gap: 30px;
    }
    .nav-option h3 {
        font-size: 20px;
    }
    .report-topic-heading,
    .item1,
    .items {
        width: 700px;
    }
    .navcontainer {
        width: 100vw;
        position: absolute;
        transition: all 0.6s ease-in-out;
        top: 0;
        left: -100vw;
    }
    .nav {
        width: 100%;
        position: absolute;
    }
    .navclose {
        left: 00px;
    }
    .searchbar {
        display: none;
    }
    .main {
        padding: 40px 30px 30px 30px;
    }
    .searchbar2 {
        width: 100%;
        display: flex;
        margin: 0 0 40px 0;
        justify-content: center;
    }
    .searchbar2 input {
        width: 250px;
        height: 42px;
        border-radius: 50px 0 0 50px;
        background-color: var(--background-color3);
        padding: 0 20px;
        font-size: 15px;
        border: 2px solid var(--secondary-color);
    }
    }

    @media screen and (max-width: 490px) {
    .message {
        display: none;
    }
    .logosec {
        width: 100%;
        justify-content: space-between;
    }
    .logo {
        font-size: 20px;
    }
    .menuicn {
        height: 25px;
    }
    .nav-img {
        height: 25px;
    }
    .nav-option {
        grid-gap: 25px;
        gap: 25px;
    }
    .nav-option h3 {
        font-size: 12px;
    }
    .nav-upper-options {
        grid-gap: 15px;
        gap: 15px;
    }
    .recent-Articles {
        font-size: 20px;
    }
    .report-topic-heading,
    .item1,
    .items {
        width: 550px;
    }
    }

    @media screen and (max-width: 400px) {
    .recent-Articles {
        font-size: 17px;
    }
    .view {
        width: 60px;
        font-size: 10px;
        height: 27px;
    }
    .report-header {
        height: 60px;
        padding: 10px 10px 5px 10px;
    }
    .searchbtn img {
        height: 20px;
    }
    }

    @media screen and (max-width: 320px) {
    .recent-Articles {
        font-size: 12px;
    }
    .view {
        width: 50px;
        font-size: 8px;
        height: 27px;
    }
    .report-header {
        height: 60px;
        padding: 10px 5px 5px 5px;
    }
    .t-op {
        font-size: 12px;
    }
    .t-op-nextlvl {
        font-size: 10px;
    }
    .report-topic-heading,
    .item1,
    .items {
        width: 300px;
    }
    .report-body {
        padding: 10px;
    }
    .label-tag {
        width: 70px;
    }
    .searchbtn {
        width: 40px;
    }
    .searchbar2 input {
        width: 180px;
    }
    }

